import Vue from 'vue'
import Router from 'vue-router'
import MySlideshow from './components/MySlideshow'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      redirect: '/1/1',
    },
    {
      path: '/:slide/:step',
      component: MySlideshow,
    },
  ],
})
